<template>
    <div style="background:#F2F6FC;padding:10px">
        <el-row>
            <el-col :span="4">
                <el-select v-model="ip" placeholder="cluster profile" @change="changegeip" filterable >
                    <el-option v-for="s in hosts" :key="s" :value="s" :label="s"></el-option>
                </el-select>
            </el-col>
            <el-col :span="3">
                <el-button type="primary" @click="add()">添加集群</el-button>
            </el-col>
            <el-col :span="3">
                <el-button type="success" @click="getTopics()">刷新数据</el-button>
            </el-col>
            <el-col :span="3">
                <el-button type="danger" @click="delCluster()">删除集群</el-button>
            </el-col>
            <el-col :span="3">
                <el-button type="primary" @click="addMonitor()">添加报警</el-button>
            </el-col>
            <el-col :span="3">
                <el-button type="primary" @click="getMonitorDatas()">查看报警阈值</el-button>
            </el-col>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <el-dialog
                            title="添加集群"
                            :visible.sync="dialogVisible"
                            width="60%"
                            :before-close="close">
                    <span>
                        <el-form ref="kafka" :model="kafka" label-width="100px">
                          <el-form-item label="集群名称:">
                            <el-input v-model="kafka.name"></el-input>
                          </el-form-item>
                            <el-form-item label="hosts:">
                            <el-input v-model="kafka.hosts" ></el-input>
                           </el-form-item>
                        </el-form>
                    </span>
                        <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="editData">添加</el-button>
                    </span>
                    </el-dialog>
                </div>
            </el-col>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <el-dialog
                            title="添加Topic阈值报警"
                            :visible.sync="dialogMonitorVisible"
                            width="60%"
                            :before-close="close">
                    <span>
                        <el-form ref="monitor" :model="monitor" label-width="100px">
                          <el-form-item label="groupId:">
                            <el-input placeholder="消费组groupId, 这个值就是应用的consumer groupId" v-model="monitor.groupId" ></el-input>
                           </el-form-item>
                          <el-form-item label="topics:">
                            <el-input placeholder="要监控的topics,多个topic 使用 , 号分割" v-model="monitor.topics" ></el-input>
                           </el-form-item>
                             <el-form-item label="limit:">
                            <el-input placeholder="报警阈值" v-model="monitor.limit" ></el-input>
                           </el-form-item>
                        </el-form>
                    </span>
                        <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogMonitorVisible = false">取 消</el-button>
                        <el-button type="primary" @click="addMonitorData">添加</el-button>
                    </span>
                    </el-dialog>
                </div>
            </el-col>
        </el-row>
        <el-row >
            <el-col :span="24">
                <div style="background:#F2F6FC;padding:5px">
                    <el-card   class="box-card" style="background:#FBFDFF;" >
                        <div slot="header" class="clearfix">
                            <span>CLUSTER INFO</span>
                        </div>
                        <el-button  v-for="o in cif" :key="o">  {{'节点: ' + o }}</el-button>
                    </el-card>
                </div>
            </el-col>

        </el-row>
        <el-row>
            <el-col :span="24">
                <div style="background:#F2F6FC;padding:5px">
                    <el-table
                            :data="tableData"
                            stripe
                            style="width: 100%"
                            size="mini"
                            @row-click="topicChart">
                        <el-table-column
                                header-align="left"
                                prop="date"
                                label="TOPICS"
                                width="180">
                            <el-table-column
                                    header-align="center"
                                    prop="topic"
                                    label="Topic"
                                    sortable
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    header-align="center"
                                    prop="partCount"
                                    label="Partitions"
                                    sortable
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    header-align="center"
                                    prop="brokerCount"
                                    label="Brokers"
                                    sortable
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    header-align="center"
                                    prop="replicaCount"
                                    label="Replicas"
                                    sortable
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    header-align="center"
                                    prop="offset"
                                    sortable
                                    label="offset">
                            </el-table-column>
                            <el-table-column
                                    header-align="center"
                                    prop="offsetLag"
                                    sortable
                                    label="offsetLag">
                            </el-table-column>
                        </el-table-column>

                    </el-table>
                </div>
            </el-col>
            <el-col :span="24">


                    <el-dialog
                            title="报警阈值查询"
                            :visible.sync="topicDialogVisible"
                            width="70%"
                            :before-close="close">

                        <div style="background:#F2F6FC;padding:5px">
                            <el-table
                                    :data="monitorData"
                                    stripe
                                    style="width: 100%"
                                    size="mini"
                                     >
                                <el-table-column
                                        header-align="center"
                                        prop="clusterName"
                                        label="ClusterName"
                                        width="180">
                                </el-table-column>
                                    <el-table-column
                                            header-align="center"
                                            prop="topics"
                                            label="Topics"
                                            sortable
                                            width="300">
                                    </el-table-column>
                                    <el-table-column
                                            header-align="center"
                                            prop="groupId"
                                            label="GroupId"
                                            sortable
                                            width="180">
                                    </el-table-column>
                                    <el-table-column
                                            header-align="center"
                                            prop="limit"
                                            label="Limit"
                                            sortable
                                            width="180">
                                    </el-table-column>
                                    <el-table-column
                                            fixed="right"
                                            label="操作"
                                            width="100">
                                        <template slot-scope="scope">
                                            <el-button @click="delMonitor(scope.row)" type="text" size="small">删除</el-button>
                                        </template>
                                    </el-table-column>

                            </el-table>
                        </div>
                        <el-button @click="topicDialogVisible = false">取 消</el-button>
                    </el-dialog>
            </el-col>
            <el-col :span="24">
                <div style="background:#F2F6FC;padding:10px">
                    <el-dialog
                            :visible.sync="topicDataLoading"
                            width="80%">
                        <i-echarts  style="width:100%;height: 500px;" :option="chartData"
                                    :loading="topicChartLoading"></i-echarts>
                    </el-dialog>

                </div>
            </el-col>
        </el-row>

    </div>

</template>

<script>
  import service from './service'
  import IEcharts from 'vue-echarts-v3/src/lite.js'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/dataZoom'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/toolbox'
  import 'echarts/lib/component/title'

  export default {
    name: 'home',
    data () {
      return {
        topicChartLoading: false,
        topicDataLoading: false,
        chartData: {
          tooltip: {
            trigger: 'axis'
          },
          title: {
            text: 'Week Data'
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          dataZoom: [{
            type: 'slider',
            start: 0,
            end: 100
          }],
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [],
            type: 'line',
            smooth: true
          }]
        },
        activeName: 'first',
        dialogVisible: false,
        dialogMonitorVisible: false,
        topicDialogVisible: false,
        monitorData: [],
        kafka: {
          name: '',
          hosts: ''
        },
        monitor: {
          name: '',
          groupId: '',
          topics: '',
          limit: ''
        },
        topicDetail: [],
        topicOffset: [],
        currTab: '1',
        currRow: {},
        homeList: [],
        topList: [],
        hosts: [],
        topics: [],
        cif: [],
        ip: '',
        chartLoading: false,
        tableData: []
      }
    },
    methods: {
      getClusterInfo () {
        return service.getClusterInfo({ host: this.ip }).then((result) => {
          this.form.used_memory = result.data.used_memory
          this.form.keys = result.data.keys
          this.form.connected_clients = result.data.connected_clients
          this.form.expires = result.data.expires
          this.form.ops = result.data.ops
          this.form.uptime = result.data.uptime
        })
      },
      changegeip () {
        this.getTopics()
      },
      getTopics() {
        service.getTopics({clusterName: this.ip}).then(res => {
          this.cif = res.data.nodes
          this.tableData = res.data.topics
          this.$notify.success({
            title: '获取TOPICS数据',
            content: `SUCC`
          })
        }).catch(error => {
          this.$message.error('获取TOPICS数据失败' + error.toString())
        })
      },
      add() {
        this.dialogVisible = true
      },
      addMonitor() {
        this.dialogMonitorVisible = true
      },
      addMonitorData() {
        service.addmonitor({clusterName: this.ip, groupId: this.monitor.groupId, topics: this.monitor.topics, limit: this.monitor.limit}).then(res => {
          this.dialogMonitorVisible = false
          this.$notify.success({
            title: '添加阈值报警成功',
            content: `SUCC`
          })
        }).catch(error => {
          this.$message.error('添加阈值报警失败' + error.toString())
        })
      },
      getMonitorDatas() {
        this.topicDialogVisible = true
        service.getMonitorDatas({}).then(res => {
          this.monitorData = res.data
          this.$notify.success({
            title: '查询阈值报警成功',
            content: `SUCC`
          })
        }).catch(error => {
          this.$message.error('查询阈值报警失败' + error.toString())
        })
      },
      close() {
        this.dialogVisible = false
        this.topicDialogVisible = false
        this.dialogMonitorVisible = false
      },
      editData() {
        service.addClusterConfig({name: this.kafka.name, bootStrap: this.kafka.hosts}).then(res => {
          this.dialogVisible = false
          this.$notify.success({
            title: '添加',
            content: `SUCC`
          })
        }).catch(error => {
          this.$message.error('操作失败' + error.toString())
        })
      },
      getClusterinfo() {
        this.axios.get('/kafka/getClustrInfo').then((res) => {
          this.hosts = res.data
        })
      },
      delCluster() {
        this.$confirm(' 此操作将删除当前 [ ' + this.ip + ' ] 集群监控, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          service.delCluster({name: this.ip})
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消删除'
          })
        })
      },
      delMonitor(row) {
        this.$confirm(' 此操作将删除当前 [ ' + row.topics + ' ] 阈值报警, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          service.delMonitor({clusterName: row.clusterName, groupId: row.groupId})
          this.topicDialogVisible = false
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消删除'
          })
        })
      },
      getTopicPartitionInfo(topic) {
        service.getTopicPartitionInfo({clusterName: this.ip, topic: topic}).then(res => {
          this.topicDialogVisible = true
          this.topicDetail = res.data.partitions
          this.topicOffset = res.data.offset
          this.$notify.success({
            title: '查询分片信息',
            content: `SUCC`
          })
        }).catch(error => {
          this.$message.error('操作失败' + error.toString())
        })
      },
      topicChart (row) {
        this.loadChart(row)
        this.topicDataLoading = true
      },
      loadChart(row) {
        this.topicChartLoading = true
        this.axios.get('/kafka/getChartByTopic', { params: { clusterName: this.ip, topic: row.topic}}).then((res) => {
          this.chartData.xAxis.data = res.data.yaxis
          this.chartData.title.text = res.data.name
          this.chartData.series = [{
            data: res.data.xaxis,
            type: 'line',
            smooth: true
          }]
        }).finally(() => {
          this.topicChartLoading = false
        })
      }
    },
    computed: {},
    mounted () {
      this.getClusterinfo()
    },
    components: {
      IEcharts
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



