import axios from 'axios'

export default {
  addClusterConfig (param, url = '/kafka/addClustrInfo') {
    return axios.get(url, {params: param})
  },
  addmonitor (param, url = '/kafka/addmonitor') {
    return axios.get(url, {params: param})
  },
  getMonitorDatas (param, url = '/kafka/getMonitorDatas') {
    return axios.get(url, {params: param})
  },
  getTopics (param, url = '/kafka/getKafkaClusterInfo') {
    return axios.get(url, {params: param})
  },
  getTopicPartitionInfo (param, url = '/kafka/describeTopics') {
    return axios.get(url, {params: param})
  },
  delCluster (param, url = '/kafka/deleteCluster') {
    return axios.get(url, {params: param})
  },
  delMonitor (param, url = '/kafka/delMonitor') {
    return axios.get(url, {params: param})
  }
}



